import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';

import PubSub, { topics } from '../../lib/pubsub';
import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

const MAX_LOADINDEX = mu.getMaxLoadIndex();

import Map from '../../components/map';
import ImageSlider from '../../components/imageSlider';
import ImageScroller from '../../components/imageScroller';
import WorkoutGraph from '../../components/workout/graph';
import TrackInfoGraph from '../../components/track/infoGraph';
import Gauge from '../../components/gauge';
import TrackComments from '../../components/track/comments';
import TrackAddComment from '../../components/track/commentAdd';

import Speed from '../../components/data/speed';
import Elevation from '../../components/data/elevation';
import Weight from '../../components/data/weight';
import Distance from '../../components/data/distance';
import Temperature from '../../components/data/temperature';

import ScrollIntoView from '../../components/util/scrollIntoView';

const initialState = {
    weather: [],
    newTrack: {},
    showAdminInfo: false,
    showWeatherDetails: false,
    submenu: 'map',
};

@observer
class TracksDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
        this.inputRef = {};
    }

    async loadTrack(props = this.props) {
		const { trackStore, userStore, workoutPlanStore } = this.props.stores;
        await trackStore.load(props.track);
        const { track } = trackStore;

        if (track.firstPoint) {
            const weather = await userStore.getWeather({
                lat: track.firstPoint[1],
                lon: track.firstPoint[0],
                altitude: track.firstPoint[2],
                force: 1,
            });
            this.setState({
                weather,
            });
        }

        workoutPlanStore.updateItem('workoutPlans', []);
		workoutPlanStore.updateItem('workoutPlan', {});
        workoutPlanStore.resetGraphPlanWeekDistanceCurrent();

        await userStore.getWorkoutSummary({ track: track.id });
    }

	deleteTrack = async () => {
		const { trackStore } = this.props.stores;
		const { track } = trackStore;
        await trackStore.saveField(track.id, 'offline', 1);
		PubSub.publish(topics.STATUS_MESSAGE, {
			message: 'Runden er slettet',
			type: 'success',
			icon: 'fas fa-check',
		});
        route(`/tracks/`);
	}

    toggleAdminInfo = () => {
		const { userStore } = this.props.stores;
		const { isAdmin } = userStore;
		if (isAdmin) {
			const { showAdminInfo } = this.state;
			this.setState({
				showAdminInfo: !showAdminInfo,
			});
		}
	}

    likeTrack = async () => {
		const { trackStore } = this.props.stores;
		const { track } = trackStore;
		const response = await trackStore.likeTrack({ id: track.id });

        if (response && response.status === 200) {
			// this.loadNewsDetail({ news });
		}
	}

	toggleAddComment = (e) => {
		const id = e.target.parentElement.dataset.id || e.target.dataset.id;
		const { showAddComment } = this.state;
		this.setState({
			showAddComment: !showAddComment,
		}, () => {
			if (this.inputRef[id]) {
				this.inputRef[id].focus();
			}
		});
	}

    toggleShowWeatherDetails = () => {
        const { showWeatherDetails } = this.state;
        this.setState({
            showWeatherDetails: !showWeatherDetails,
        })
    }

    chooseSubmenu = (e) => {
		const submenu = e.target.parentElement.dataset.menu || e.target.dataset.menu;
		this.setState({ submenu });
	}

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }

	componentDidMount() {
        this.loadTrack();
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.track !== this.props.track) {
            this.loadTrack(nextProps);
        }
    }

	render() {
		const { trackStore, userStore } = this.props.stores;
        const { submenu, showAdminInfo, showAddComment, weather, showWeatherDetails } = this.state;
        const { isPublic } = this.props;

        const { isAdmin, graphWorkoutSummaryWeekDistanceCurrentTotal, user = {} } = userStore;
        const { teams = [] } = user;
		const { track = {} } = trackStore;
        const { gpx, gpxInfo = {} } = track;
        const { weightedTotalKcal = 0, loadIndex = 0 } = gpxInfo;
		const loadMultiplier = loadIndex / 100;

        const isAllowedToEdit = track.user === user.id || teams.indexOf(track.team) > -1;

        const { likes = [], comments = [] } = track;
		const hasLikedNews = track.likes && track.likes.findIndex(e => e.user === user.id) > -1;

		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link pl-0 fixed-top text-primary'
                            style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                            onClick={this.back}>
                            <i class='fas fa-arrow-circle-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>&nbsp;</h5>
                    </div>
                    <div>
                        {isAllowedToEdit && <a class='btn btn-sm btn-primary float-right'
                            href={`/tracks/edit/${track.id}`}
                        >
                            <i class='fas fa-edit' /> <Text id='tracks.edit'>Endre</Text>
                        </a>}
                    </div>
                </div>

                <div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <div class='d-flex mb-2'>
                            {/* {JSON.stringify(dog, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={track.image ? `background-image: url("${track.image.s3SmallLink}");` : ''}>
                                {!track.image && <i class='fas fa-map-marked text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3'>
                                <h5>{track.name}</h5>
                                <div>{track.place}</div>
                                <i class='fas fa-calendar-alt mr-1' />
                                {track.gpx && track.gpx.s3Link && <span class='float-right font-weight-lighter'><a href={track.gpx.s3Link} native>
                                    <i class='fas fa-download' /> {util.formatBytes(track.gpx.bytes, 0)}
                                </a></span>}
                                {util.isoDate(track.date || track.updatedDate || track.createdDate)}<br />
                                {track.public === 1 && <span class='badge badge-success'>Is public</span>}
                                {isPublic && track.userObject && <span class='text-muted font-weight-lighter ml-3'>{mu.displayName(track.userObject)}</span>}
                            </div>
                        </div>
                    </div>
                </div>

                <div class='row'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3'>
						<div class='row'>
                            <div class='w-100 overflow-hidden'>
                                <div
                                    class='d-flex flex-row flex-nowrap pb-2 px-1'
                                    style='overflow: auto; scroll-snap-type: x mandatory;'
                                >
                                    <div class='col px-0 pt-2'>
                                        <button
                                            class={`btn btn-block rounded-pill-left rounded-none ${submenu === 'map' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                                            style='text-overflow: ellipsis; overflow: hidden;'
                                            data-menu='map'
                                            onClick={this.chooseSubmenu}
                                        ><i class='fas fa-map-marked-alt' /> <Text id='tracks.map'>Map</Text></button>
                                        {submenu === 'map' && <ScrollIntoView horizontal={'center'} vertical={'end'} />}
                                    </div>
                                    <div class='col px-0 pt-2'>
                                        <button
                                            class={`btn btn-block rounded-none ${submenu === 'stats' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                                            style='text-overflow: ellipsis; overflow: hidden;'
                                            data-menu='stats'
                                            onClick={this.chooseSubmenu}
                                        ><i class='fas fa-chart-line' /> <Text id='tracks.stats'>Your stats</Text></button>
                                        {submenu === 'stats' && <ScrollIntoView horizontal={'center'} vertical={'end'} />}
                                    </div>
                                    <div class='col px-0 pt-2'>
                                        <button
                                            class={`btn btn-block rounded-none ${submenu === 'weather' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                                            style='text-overflow: ellipsis; overflow: hidden;'
                                            data-menu='weather'
                                            onClick={this.chooseSubmenu}
                                        ><i class='fas fa-cloud-sun-rain' /> <Text id='tracks.weather'>Weather</Text></button>
                                        {submenu === 'weather' && <ScrollIntoView horizontal={'center'} vertical={'end'} />}
                                    </div>
                                    <div class='col px-0 pt-2'>
                                        <button
                                            class={`btn btn-block rounded-none ${submenu === 'summary' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                                            style='text-overflow: ellipsis; overflow: hidden;'
                                            data-menu='summary'
                                            onClick={this.chooseSubmenu}
                                        ><i class='fas fa-info-circle' /> <Text id='tracks.summary'>Summary</Text></button>
                                        {submenu === 'summary' && <ScrollIntoView horizontal={'center'} vertical={'end'} />}
                                    </div>
                                    <div class='col px-0 pt-2'>
                                        <button
                                            class={`btn btn-block rounded-pill-right rounded-none ${submenu === 'photos' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                                            style='text-overflow: ellipsis; overflow: hidden;'
                                            data-menu='photos'
                                            onClick={this.chooseSubmenu}
                                        ><i class='fas fa-camera' /> <Text id='tracks.photos'>Photos</Text></button>
                                        {submenu === 'photos' && <ScrollIntoView horizontal={'center'} vertical={'end'} />}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                {submenu === 'map' && <>
                    <div class='row mt-4'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 p-0'>
                            {track.gpx && <div class='mb-4' style='height: 500px;'>
                                <Map
                                    stores={this.props.stores}
                                    mapId={track.id}
                                    geojson={track.gpx.geoJSON}
                                    gpxInfo={track.gpxInfo || track.gpx.gpxInfo}
                                    fullscreenMap={true}
                                />
                            </div>}
                        </div>
                    </div>
                </>}

                {submenu === 'stats' && <>
                    <div class='row mt-4'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                            <h5><Text id='tracks.your-workout-summary'>Your workout summary</Text></h5>
                            {graphWorkoutSummaryWeekDistanceCurrentTotal > 0 ? <>
                                <WorkoutGraph stores={this.props.stores} isLoading={false} />
                                {/* {util.format(graphWorkoutSummaryWeekDistanceCurrentTotal, 0)} km */}
                            </> : <>
                                <div class='text-center text-muted'>
                                    <div class='display-1'>
                                        <i class='fas fa-chart-line' />
                                    </div>
                                    <h3 class='font-weight-lighter'>
                                        <Text id='tracks.no-workout-summary'>You have no workout summary for this track. You can choose track inside your workouts to have the summary calculated.</Text>
                                    </h3>
                                </div>
                            </>}
                        </div>
                    </div>
                </>}

                {submenu === 'weather' && <>
                    <div class='row mt-4'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                            <h5><Text id='tracks.weather-start'>Weather at the start position</Text></h5>

                            <div class='row'>
                                {weather && weather.length > 0 ? weather.slice(0, 4).map(w => (
                                    <div class='col-3 text-center'>
                                        <img loading='lazy' src={`./assets/svg/${util.getNestedValue(w, 'next_1_hours.summary.symbol_code')}.svg`} /><br />
                                        <nobr>
                                            <small>
                                                <i class='fas fa-temperature-low text-muted ml-1' />
                                            </small> {util.getNestedValue(w, 'instant.details.air_temperature')} <span class='text-muted'>C</span>
                                        </nobr><br />
                                        <small>
                                            <i class='fas fa-compass text-muted ml-1' /> {mu.windDirection(util.getNestedValue(w, 'instant.details.wind_from_direction'))}<br />
                                            <i class='fas fa-wind text-muted ml-1' /> {util.getNestedValue(w, 'instant.details.wind_speed')} <span class='text-muted'>m/s</span><br />
                                            <i class='fas fa-cloud-rain text-muted ml-1' /> {util.getNestedValue(w, 'next_1_hours.details.precipitation_amount')} <span class='text-muted'>mm</span><br />
                                            <i class='fas fa-tint text-muted ml-1' /> {util.getNestedValue(w, 'instant.details.relative_humidity')} <span class='text-muted'>%</span><br />
                                        </small>
                                        <span class='font-weight-strong'>{util.isoTime(w.time)}</span><br />
                                    </div>
                                )) : <>                        
                                    <div class='text-center text-muted'>
                                        <div class='display-1'>
                                            <i class='fas fa-cloud-sun-rain' />
                                        </div>
                                        <h3 class='font-weight-lighter'>
                                            <Text id='tracks.no-weather'>Can't find weather for this track.</Text>
                                        </h3>
                                    </div>
                                </>}
                            </div>
                            <small>
                                {showWeatherDetails ? <>
                                    <table class='table table-sm table-striped mt-4'>
                                        {weather && weather.length > 0 && weather.slice(4, 70).map(w => (
                                            <tr>
                                                <td><img loading='lazy' src={`./assets/svg/${util.getNestedValue(w, 'next_1_hours.summary.symbol_code') || util.getNestedValue(w, 'next_6_hours.summary.symbol_code')}.svg`} style='height: 20px;' /></td>
                                                <td class='text-center'><i class='fas fa-temperature-low text-muted ml-1' /> {util.getNestedValue(w, 'instant.details.air_temperature')} <span class='text-muted'>C</span></td>
                                                <td class='text-center'><i class='fas fa-compass text-muted ml-1' /> {mu.windDirection(util.getNestedValue(w, 'instant.details.wind_from_direction'))}</td>
                                                <td class='text-center'><i class='fas fa-wind text-muted ml-1' /> {util.getNestedValue(w, 'instant.details.wind_speed')} <span class='text-muted'>m/s</span></td>
                                                <td class='text-center'><i class='fas fa-cloud-rain text-muted ml-1' /> {util.getNestedValue(w, 'next_1_hours.details.precipitation_amount') || util.getNestedValue(w, 'next_6_hours.details.precipitation_amount') || 0} <span class='text-muted'>mm</span></td>
                                                <td class='text-center'><i class='fas fa-tint text-muted ml-1' /> {util.getNestedValue(w, 'instant.details.relative_humidity')} <span class='text-muted'>%</span></td>
                                                <td class='font-weight-strong text-right'>
                                                    {util.isoDate(new Date(), false, false, true) !== util.isoDate(w.time, false, false, true) 
                                                        ? util.formatDate(w.time, { year: undefined, weekday: 'short', localeMatcher: 'lookup' }) : ''} {util.isoTime(w.time)}
                                                </td>
                                            </tr>
                                        ))}
                                    </table>
                                    <button class='btn btn-link' onClick={this.toggleShowWeatherDetails}><i class='fas fa-eye-slash' /> <Text id='tracks.weather-hide-details'>Hide details</Text></button>
                                </>
                                : <button class='btn btn-link' onClick={this.toggleShowWeatherDetails}><i class='fas fa-eye' /> <Text id='tracks.weather-show-details'>Show details</Text></button>}
                            </small>
                        </div>
                    </div>
                </>}

                {submenu === 'summary' && <>
                    <div class='row mt-4'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                            <h5><Text id='tracks.summary'>Track summary</Text></h5>
                            <p><Text id='tracks.summary-info'>The summary for this track is estimated based on the tracks profile and your team settings.</Text></p>
                        </div>
                    </div>
                    <div class='row mt-2'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                            <div class='row'>
                                <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-road' /> <Text id='tracks.distance'>Distance</Text></small>
                                    <h3>
                                        <Distance stores={this.props.stores} value={track.distanceKm} />
                                    </h3>
                                    {gpxInfo.calculatedTotalDistance > 0 && <small class='text-muted' style='line-height: 0.8em;'>
                                        <Text id='workouts.kcal-calculated'>Beregnet:</Text> <Distance stores={this.props.stores} value={gpxInfo.calculatedTotalDistance} /><br />
                                    </small>}
                                </div>
                                <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> <Text id='tracks.elevation'>Elevation</Text></small>
                                    <h3>
                                        <Elevation stores={this.props.stores} value={track.elevation} />
                                    </h3>
                                    {gpxInfo.calculatedAscend > 0 && <small class='text-muted' style='line-height: 0.8em;'>
                                        <Text id='workouts.kcal-calculated'>Beregnet:</Text> <Elevation stores={this.props.stores} value={gpxInfo.calculatedAscend} /><br />
                                    </small>}
                                </div>
                                <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> <Text id='tracks.downhill'>Downhill</Text></small>
                                    <h3>
                                        <Elevation stores={this.props.stores} value={track.decent} />
                                    </h3>
                                    {gpxInfo.calculatedDescend > 0 && <small class='text-muted' style='line-height: 0.8em;'>
                                        <Text id='workouts.kcal-calculated'>Beregnet:</Text> <Elevation stores={this.props.stores} value={gpxInfo.calculatedDescend} /><br />
                                    </small>}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class='row mt-2'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                            <div class='row'>
                                <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-clock' /> <Text id='tracks.duration'>Duration</Text></small>
                                    <h3>
                                        {util.secToHms(gpxInfo.duration)}
                                    </h3>
                                </div>
                                {gpxInfo.normalSpeedMs > 0 ? <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> <Text id='tracks.speed'>Speed</Text></small>
                                    <h3>
                                        <Speed stores={this.props.stores} value={gpxInfo.normalSpeedMs * 3.6} />
                                    </h3>
                                </div> : <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-tachometer-alt' /> <Text id='tracks.speed'>Speed</Text></small>
                                    <h3>
                                        <Speed stores={this.props.stores} value={gpxInfo.normalSpeed} />
                                    </h3>
                                </div>}
                            </div>
                        </div>
                    </div>

                    <div class='row mt-2'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                            {(util.checkNested(track, 'gpxInfo', 'elevations') || util.checkNested(track, 'gpx', 'gpxInfo', 'elevations')) && <TrackInfoGraph
                                stores={this.props.stores}
                                showTemperatur={false}
                                key={`track-elevation-${track.id}`}
                            />}
                        </div>
                    </div>

                    {loadIndex > 0 && <div class='row mt-2'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                            <h5 onClick={this.toggleAdminInfo}>
                                <Text id='workouts.load-index'>Belastningsindex</Text>
                            </h5>
                        </div>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-center'>
                            <Gauge key={`gauge-${loadIndex}`}
                                radius={100}
                                showPercentText={true}
                                color={mu.getLoadIndexColor(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}
                                percent={Math.floor(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}
                                completed={mu.getLoadIndexText(util.normalizeBetween(loadIndex, 0, MAX_LOADINDEX, 0, 100))}
                                prefix=''
                            />
                            <div>
                                <span class='badge badge-pill badge-secondary'>Long distance</span>
                                <span class='badge badge-pill badge-secondary ml-2'>Serious amature</span><br />
                            </div>
                            
                            {isAdmin && showAdminInfo && <div>
                                <small>
                                    <span class='badge badge-secondary mr-2'>loadIndex: {gpxInfo.loadIndex}</span>
                                    <span class='badge badge-secondary mr-2'>Max loadIndex: {MAX_LOADINDEX}</span>
                                    <span class='badge badge-secondary mr-2'>totalKcalRelated: {util.format(gpxInfo.totalKcalRelated, 1)}</span>
                                    <span class='badge badge-secondary mr-2'>normalSpeedMs: {gpxInfo.normalSpeedMs}</span>
                                    <span class='badge badge-secondary mr-2'>normalDuration: {gpxInfo.normalDuration}</span>
                                    <span class='badge badge-secondary mr-2'>durationFactor: {util.format(gpxInfo.durationFactor, 2)}</span>
                                    <div>
                                        <small class='text-muted'>
                                            loadIndex = Math.floor(totalKcalRelated * durationFactor * speedFactor * typeFactor * ambitionFactor);
                                        </small>
                                    </div>
                                </small>
                            </div>}
                        </div>
                    </div>}
                </>}

                {submenu === 'photos' && <>
                    <div class='row mt-4'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                            <h5><Text id='tracks.photos'>Photos from this track</Text></h5>
                        </div>
                    </div>
                    <div class='row'>
                        <Localizer>
                            <ImageScroller stores={this.props.stores} images={track.images} noimagestext={<Text id='tracks.no-photos'>No photos for this track.</Text>} />
                        </Localizer>
                    </div>
                </>}


                <div class='row'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-3'>
                        {likes && likes.length > 0 && <small class='bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                            <i class='fas fa-thumbs-up text-primary' /> {likes.length}
                        </small>}
                        {comments && comments.length > 0 && <small class='float-right bg-light shadow-sm rounded-lg py-0 px-1' style='bottom: 20px; right: 15px;'>
                            <i class='fas fa-comment text-primary' /> {comments.length}
                        </small>}
                    </div>

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-3'>
                        <div class='row'>
                            <div class='col-6 text-center'>
                                <button type='button' class={`btn btn-outline-${hasLikedNews ? 'primary' : 'secondary'} btn-lg btn-block ${hasLikedNews ? 'text-primary' : ''}`}
                                    onClick={this.likeTrack}
                                    // disabled={hasLikedNews}
                                >
                                    <i class={`${hasLikedNews ? 'text-primary fas' : 'far'} fa-thumbs-up`} /> Like
                                </button>
                            </div>
                            <div class='col-6 text-center'>
                                <button type='button' class='btn btn-outline-secondary btn-lg btn-block'
                                    data-id={track.id}
                                    onClick={this.toggleAddComment}
                                >
                                    <i class='fas fa-comment' /> Comment
                                </button>
                            </div>
                        </div>
                    </div>

                    {showAddComment && <TrackAddComment
                        stores={this.props.stores}
                        track={track}
                        // callback={this.loadNewsDetail}
                        inputRef={c => this.inputRef[track.id] = c}
                        inputRefObject={this.inputRef}
                    />}
                    <TrackComments
                        stores={this.props.stores}
                        track={track}
                        cols='12'
                        // callback={this.loadNewsDetail}
                    />

                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-5'>
                        {isAllowedToEdit && <button class='btn btn-sm btn-danger mt-5 float-right'
                            onClick={this.deleteTrack}
                        >
                            <i class='fas fa-trash' /> <Text id='tracks.delete'>Slett</Text> {track.name}
                        </button>}
                    </div>
                </div>
			</div>
		);
	}
}

export default TracksDetail;
